          p.mb-0 El #[em marketing] digital no tiene diferencias abismales con el convencional, salvo aquello que lo define < lo digital >; es decir, el entorno donde sucede la interaccin entre la marca y el consumidor, desplazando en muchas ocasiones las tiendas fsicas, la interaccin #[em face to face] y los medios masivos convencionales. 
        p.mb-0 El #[em marketing] digital permite que las marcas (paradjicamente) sean ms cercanas a los mercados objetivo, que llegan a pblicos ms especficos creando as comunidades ms cerradas y menos masivas. Un anuncio en televisin tena que ser visto por todos los miembros de la familia que en el horario prime time estuviesen viendo la telenovela de moda, de esa forma el anuncio de la cuchilla de afeitar dirigido al padre impactaba de igual forma a la madre, a los hijos y eventualmente a otros miembros de la familia o amigos.

