        .cajon.color-secundario.p-5.mb-5
          p.mb-0 Las #[em cookies] son pequeos ficheros de texto con informacin sobre el usuario. Estas se almacenan en el navegador, desde donde el usuario puede aceptarlas, bloquearlas o eliminarlas. 
        p.mb-4 A esta informacin que se almacena en las #[em cookies] se puede acceder posteriormente con distintos fines como ofrecer publicidad, hacer #[em remarketing] (anuncios personalizados dirigidos a usuarios que visitaron previamente tu #[em web]), conocer el nmero total de sesiones generadas por los usuarios, mantener los productos aadidos en las cestas de la compra de las tiendas #[em online], usar aplicaciones como las de los bancos sin necesidad de iniciar sesin en cada pgina, etc.
        p.mb-0 Las #[em cookies] tienen varias clasificaciones y criterios: 
    
    
    .row.align-items-top.justify-content-center.mb-4
      .col-lg-11.col-md-12.col-12
        .row.align-items-center.justify-content-center.mb-0
