          .col-lg-7.col-md-12.col-12
            .tarjeta.tarjeta--verde
              SlyderA(tipo="b").slider-a-custom.p-4
                .row.align-items-top
                  .col-lg-12.col-md-12.col-12
                    p #[b 1. #[em Cookies] propias o de primera parte:] son las generadas y almacenadas nica y exclusivamente por el dominio al que se est accediendo. Por ejemplo, las que se utilizan en las tiendas #[em online]. A medida que vas aadiendo productos al carrito de la compra, la informacin se almacena en una #[em cookie] que permite que en las siguientes pginas se puedan mantener los mismos productos. Las herramientas de medicin como Google #[em Analytics] usan #[em cookies] de primera parte para hacer el seguimiento de las interacciones de los usuarios: para saber de dnde vienen, cuntas sesiones generan, qu pginas visitan, etc.
                .row.align-items-top
                  .col-lg-12.col-md-12.col-12
                    p #[b 2. #[em Cookies] de terceros o de tercera parte:] son las generadas y utilizadas por un dominio externo al del sitio #[em web] que estn visitando los usuarios. Por ejemplo, cuando accedes a una #[em web] y ves #[em banners] sobre productos que te interesan, esa publicidad no se produce de forma aleatoria. Esto ocurre porque en las cookies se guardan tus preferencias de navegacin, lo que permite a los anunciantes mostrarte anuncios adaptados a tus gustos a travs de otras #[em webs.]
                .row.align-items-top
                  .col-lg-12.col-md-12.col-12
                    p #[b 3. #[em Cookies] de sesin:] son las que se eliminan en cuanto caduca la sesin. Su propsito es permitirte moverte entre las diferentes pginas de una #[em web] sin necesidad de que tengas que dar la misma informacin una y otra vez. Por ejemplo, las que se utilizan en las tiendas #[em online]. A medida que vas aadiendo productos al carrito de la compra, la informacin se almacena en una #[em cookie] que permite que en las siguientes pginas se puedan mantener los mismos productos.
                .row.align-items-top
                  .col-lg-12.col-md-12.col-12
                    p.mb-4 #[b 4. #[em Cookies] persistentes:] son las que permiten almacenar los datos de los usuarios durante ms tiempo. Tambin tienen fecha de caducidad, la cual es definida por el responsable de la #[em cookie.]
                    p.mb-0 En el caso de que el usuario no elimine estas #[em cookies] manualmente desde su navegador, cuando lleguen a su fecha de caducidad estas se eliminarn completamente de este.
                .row.align-items-top
                  .col-lg-12.col-md-12.col-12
                    p.mb-4 #[b 5. #[em Cookies] tcnicas:] son las que permiten a los usuarios navegar a travs de una pgina #[em web] o aplicacin y utilizar sus servicios.
                    p.mb-0 Por ejemplo, las que te permiten recordar los productos en el carrito de la compra o registrarte en un evento.
                .row.align-items-top
                  .col-lg-12.col-md-12.col-12
                    p.mb-4 #[b 6. #[em Cookies] de personalizacin:] son las que permiten al usuario disfrutar del servicio que presta la #[em web] o aplicacin de una forma personalizada.
                    p.mb-0 Por ejemplo, cuando una #[em web] est traducida a muchos idiomas, el que t la veas en el tuyo es posible gracias a las #[em cookies] de personalizacin.
                .row.align-items-top
                  .col-lg-12.col-md-12.col-12
                    p #[b 7. #[em Cookies] de anlisis:] son las que permiten registrar y almacenar los datos sobre los usuarios para posteriormente analizar su comportamiento.
                .row.align-items-top
                  .col-lg-12.col-md-12.col-12
                    p #[b 8. #[em Cookies] publicitarias:] son #[em cookies] cuyo nico propsito es hacer publicidad.
                .row.align-items-top
                  .col-lg-12.col-md-12.col-12
                    p #[b 9. #[em Cookies] de publicidad comportamental:] son las #[em cookies] que permiten hacer publicidad a los usuarios, pero de una forma ms personalizada. Cmo? Recabando informacin de su comportamiento, para as mostrarles publicidad basada en dicho comportamiento.
    
