    p.mb-4 Un #[em KPI] es un indicador clave de rendimiento, esto quiere decir que gracias a este tipo de indicadores es posible saber si se estn cumpliendo o no los objetivos. Algunos ejemplos de #[em KPI] son: #[em ROI], tasa de rebote.
    
