            h2 #[em ROI (Return On Investment)]
            p El ROI es el retorno de la inversin, es decir, qu cantidad de la que has invertido ha vuelto a la empresa. Este KPI mide la relacin existente entre el beneficio neto y los costos. El mtodo exacto para realizar el clculo depende de los objetivos de la inversin que la empresa realice. Su frmula es: 
            p.mb-4 ROI = [(ingresos - inversin) / inversin] * 100
          .col-md.tarjeta.color-primario.pri9.f2.p-5
            .row.justify-content.mb-3
