          p.mb-0 Es un proceso de relacionamiento con los clientes de una empresa o marca, su enfoque est en el proceso de comercializacin, mercadeo y comunicacin de la empresa con sus clientes reales y potenciales.
        p.mb-0 Este proceso depende en casi un 100 % del #[em software] que gestiona la informacin de los clientes, para luego con base en estos datos generar acciones especficas de promocin y comercializacin. El #[em CRM] se nutre de los datos de los clientes a travs de Sistemas de Informacin de Mercados (#[em SIM]); estos datos abundan en medios como las redes sociales y los consumidores los entregan de forma libre y voluntaria; adems, son muy eficaces para el desarrollo de estrategias puntuales de #[em marketing.]

    .row.align-items-center.justify-content-center.mb-4
