            .col-lg-10.col-md-12.col-12
              h4.mb-0.text-white #[b El #[em CRM] evolucion con la llegada de las redes sociales y hoy por hoy se conoce como #[em Social CRM,] mejorando el sistema de interaccin entre la marca y el cliente, convirtiendo la comunicacin de doble va y no nicamente desde la marca hacia el consumidor. ]
    
    p.mb-4 De esta forma la comunicacin que se realiza a travs del #[em Social CRM] es mucho ms integral, pues el consumidor es alcanzado por un correo electrnico que se le ha dejado cuando hizo su suscripcin en el sitio #[em web] de la empresa, generndole impactos que se #[em postean] en las redes sociales que ha seguido gracias a una campaa de activacin, la cual se hace peridicamente para captar nuevos clientes. De la misma forma recibe un mensaje de texto en su celular y mensajes de WhatsApp, todo esto en un periodo de tres semanas en las que la marca ha estado interactuando con el consumidor, creando posicionamiento y recordacin.
    

