    p.mb-4 En el enfoque del Social #[em CRM] existen cuatro elementos bsicos para tener en cuenta:

    TabsC.color-primario.mb-0
      .py-3.py-md-4.bg26.px-3(titulo="1. Inteligencia de clientes")
        .row.justify-content-center
          .col-lg-5.my-3
            img.img-a.img-t.d-none.d-lg-block(src='@/assets/curso/tema2/img28.png', alt='')
          .col-lg-7.col-md-12.col-12.my-5
            h4.mb-4 1. Inteligencia de clientes
            p Se necesita tener conocimiento sobre el cliente para poder desarrollar productos y servicios enfocados a sus expectativas. Para convertir los datos en conocimiento, se emplean bases de datos y reglas a partir de un proceso de segmentacin de la informacin.
                
      .py-3.py-md-4.bg26.px-3(titulo="2. Interactividad")
        .row.justify-content-center
          .col-lg-5.my-3
            img.img-a.img-t.d-none.d-lg-block(src='@/assets/curso/tema2/img29.png', alt='')
          .col-lg-7.col-md-12.col-12.my-5
            h4.mb-4 2. Interactividad
            p El proceso de comunicacin pasa de un monlogo: de la empresa al cliente, a un dilogo, entre ellos. Adems, es el cliente el que dirige el dilogo y decide cundo empieza y cundo acaba.
            
      .py-3.py-md-4.bg26.px-3(titulo="3. Fidelizacin de clientes")
        .row.justify-content-center
          .col-lg-5.my-3
            img.img-a.img-t.d-none.d-lg-block(src='@/assets/curso/tema2/img30.png', alt='')
          .col-lg-7.col-md-12.col-12.my-5
            h4.mb-4 3. Fidelizacin de clientes
            p En la prctica, es mucho mejor y ms rentable fidelizar a los clientes que adquirir nuevos. La fidelizacin de los clientes pasa a ser muy importante y, por tanto, se convierte en el pilar de la gestin del #[em marketing] relacional en funcin del ciclo de vida del cliente. El #[em  community manager] debe considerar la manera como se establece fidelizacin proponiendo y utilizando las herramientas de social media #[em marketing.]
            
      .py-3.py-md-4.bg26.px-3(titulo="4. Personalizacin")
        .row.justify-content-center
          .col-lg-5.my-3
            img.img-a.img-t.d-none.d-lg-block(src='@/assets/curso/tema2/img31.png', alt='')
          .col-lg-7.col-md-12.col-12.my-5
            h4.mb-4 4. Personalizacin
            p Cada cliente desea recibir promociones y ofertas personalizadas, por lo que se necesitan grandes esfuerzos en inteligencia de las bases de datos para desarrollar una eficiente segmentacin de clientes. La customizacin del mensaje, en fondo y en forma, aumenta drsticamente la eficacia de las acciones de comunicacin.


