          p.mb-0 Inicialmente el #[em SEO] (del ingls #[em Search Engine Optimization]) se enmarca en las estrategias del #[em marketing] relacional y el colaborativo, que tienden a primar el buscador como canal de publicidad para pblicos muy segmentados (aquellos que utilicen cierta bsqueda concreta) y, finalmente, el ideal de una estrategia de #[em SEO] implica crear una red propia de usuarios fidelizados que hablen bien y proactivamente de la marca.
        p.mb-0 Por eso es importante primero pensar qu tipo de estrategia global se va a llevar a cabo, y en funcin de esta, pensar en l y no antes. As, en la relacin entre #[em marketing] y #[em SEO] se debe tener en cuenta: #[em marketing] no es #[em SEO] y #[em SEO] no es #[em marketing;] el #[em marketing] es un concepto ms amplio que el #[em SEO]. Primero se debe estudiar la estrategia de #[em marketing] y su continuacin. La unin de #[em marketing] + #[em SEO] debe llevar a visitas que lleven a acciones y posiblemente esas acciones lleven a ventas o conversiones. Serrano (2015).
    
    .row.justify-content-start.mb-5
