            p.mb-0 El #[em SEM] (del ingls #[em Search Engine Marketing]) a diferencia de #[em SEO,] utiliza herramientas que los buscadores ofrecen para el desarrollo de estrategias de posicionamiento a travs de anuncios publicitarios pagos, como es el caso de Google Adwords; esto requiere la seleccin de palabras claves, creacin de anuncios y gestin de pauta. Se pueden realizar a travs de #[em PPC] (Pago Por Clic) y #[em PCP] (Costo Por Clic).

    .row.align-items-center.justify-content-center.mb-0
