              SlyderA(tipo="b")
                .row
                  .col-lg-12.col-md-12.col-12
                    p #[b Segmentacin del mercado:] la segmentacin en esta red social es tal vez una de las ms especficas que se puedan realizar en una estrategia de #[em marketing,] gracias a la caracterizacin que los mismos usuarios realizan en sus perfiles y toda la informacin que en ellos generan, de esta forma no solo se puede hacer una segmentacin con datos demogrficos, sino tambin con caractersticas personales muy concretas, gustos a todo nivel, intereses, temas de consulta, palabras claves, grupos de influencia, entre muchos otros datos.
                .row
                  .col-lg-12.col-md-12.col-12
                    p #[b Viralizacin:] el pago de anuncios en Facebook adems de garantizar el alcance de miles o quizs millones de personas es solo el primer paso para hacer un anuncio viral, quienes son alcanzados por un anuncio tienen la opcin de compartirlo con sus contactos, esto significa que si aquello que se publica es de inters del mercado objetivo, puede viralizarse por iniciativa de los mismos usuarios, de ah la importancia del desarrollo de contenidos atractivos, creativos y de calidad.
                .row
                  .col-lg-12.col-md-12.col-12
                    p #[b Interactividad:] las redes sociales y en particular Facebook, ofrecen una amplia gama de posibilidades de interaccin que permite que la marca y el mercado objetivo tengan una comunicacin de doble va y este ltimo otorgue a la empresa datos que sean muy relevantes para el desarrollo de futuras estrategias de comunicacin y #[em marketing.]
                .row
                  .col-lg-12.col-md-12.col-12
                    p #[b Costo:] las estrategias que se realicen en Facebook suelen ser econmicas debido al sistema de pago por clic, en el que solo se paga por los clics que se hagan en los anuncios y generen respuestas efectivas.               
                .row
                  .col-lg-12.col-md-12.col-12
                    p #[b Medicin:] uno de los aspectos ms relevantes para cualquier estrategia de comunicacin, es el acceso a datos estadsticos reales que permitan conocer el nivel de efectividad que un anuncio o campaa tiene, esto permite determinar el ROI de la estrategia y realizar correcciones para lograr los objetivos y el desarrollo de campaas futuras.

