      h2 2.8 #[em Inbound marketing]
    
    .row.align-items-center.justify-content-center.mb-4
      .col-lg-4.col-md-9.col-9
        img.img-a.img-t.mb-4(src='@/assets/curso/tema2/img55.png', alt='Texto que describa la imagen')
      .col-lg-8.col-md-12.col-12
        .cajon.color-secundario.p-4.mb-4
          p.mb-0 Es tal vez la metodologa que presenta mayor eficacia en el desarrollo de una estrategia de #[em marketing,] la cual tenga como finalidad la atraccin del mercado potencial para hacerlo adepto a una marca por su propia conviccin e iniciativa.
        p.mb-0  #[em El inbound marketing] crea experiencias significativas que generan valor para el mercado objetivo de la empresa, esto se realiza con el desarrollo de contenido til y relevante para los usuarios de un sitio #[em web], al mismo tiempo que se interacta con ellos a travs de medios ms personales y con la posibilidad de generar un nivel de conversacin ms personal, como el correo electrnico, los mensajes de texto y los chats. No tiene un enfoque promocional o publicitario, por el contrario, el contenido generado representa mayor utilidad y relevancia, lo que hace que el mercado potencial se acerque a la marca de forma orgnica. Un ejemplo de ello es el modelo aplicado en escuelas de ingls o institutos donde se dictan estos tipos de cursos, enfatizndose siempre en una conversacin ms cercana.

    p.mb-4 El #[em inbound marketing] se enfoca en tres momentos especficos: atraer, conversar, seducir:

    AcordionA(tipo="b" clase-tarjeta="tarjeta tarjeta--grissss").mb-0
      .row.p-4(titulo="Atraer")
        .col-md-1
          figure
            img.img-a.img-t.d-none.d-lg-block(src='@/assets/curso/tema2/img56.svg', alt='Texto que describa la imagen')
        .col-md-10.col-lg-11.col-12.mb-4.mb-md-0
          p.mb-4 En este punto se atraen a todos aquellos clientes potenciales; es decir, aquellos que tienen la opcin de convertirse en compradores efectivos del producto o servicio. para ello, es indispensable desarrollar contenido relevante y muy atractivo, adicional de llegar a ellos con anuncios en redes sociales y medios ms directos. Atraer significa demostrar a los prospectos que pueden confiar en la marca para resolver un problema en particular o para satisfacer una necesidad. Las personas buscan respuestas a preguntas que tienen en su consciente e inconsciente y si estn relacionados con un producto o servicio en particular es viable utilizar conocimiento e informacin que resulte til para resolver dichas preguntas y atraer al mercado especfico. Para este punto se puede hacer uso de algunas herramientas muy puntuales:
          ul.lista-ul--color
            li.d-flex.mb-0
              i.fas.fa-plus.mas
              p.mb-0 #[b Anuncios]
            li.d-flex.mb-0
              i.fas.fa-plus.mas
              p.mb-0 #[b Videos]
            li.d-flex.mb-0
              i.fas.fa-plus.mas
              p.mb-0 #[b Publicaciones en blogs]
            li.d-flex.mb-0
              i.fas.fa-plus.mas
              p.mb-0 #[b Redes sociales]
            li.d-flex.mb-0
              i.fas.fa-plus.mas
              p.mb-0 #[b Diseo de contenido]
      .row.p-4(titulo="Conversar")
        .col-md-1
          figure
            img.img-a.img-t.d-none.d-lg-block(src='@/assets/curso/tema2/img57.svg', alt='Texto que describa la imagen')
        .col-md-10.col-lg-11.col-12.mb-4.mb-md-0
          p.mb-4 En este momento ya se ha identificado un grupo de prospectos que permite ir un paso adelante en el proceso de venta, tambin se han diferenciado los medios por los que dichos prospectos prefieren entablar una determinada conversacin: correo electrnico, #[em chat], sitio #[em web] o redes sociales; con estos datos ya se puede acceder a informacin ms personal de estos usuarios a travs de un mtodo de conversin como un formulario de suscripcin. Posteriormente, esta informacin se utiliza para realizar estrategias de #[em marketing] relacional con herramientas #[em CRM] que generen experiencias personalizadas, a la medida de cada miembro del mercado objetivo. En este punto es necesario hacer una diferenciacin de los usuarios e identificar cules de ellos son los ms adeptos y cercanos a la marca para comenzar todo un programa que permita cautivarlos y llevarlos al siguiente nivel, donde por medio de estrategias de interaccin se hacen ofertas concretas de los productos o servicios que la empresa ofrece. En este punto se puede hacer uso de las siguientes herramientas:
          ul.lista-ul--color
            li.d-flex.mb-0
              i.fas.fa-plus.mas
              p.mb-0 #[em CRM]
            li.d-flex.mb-0
              i.fas.fa-plus.mas
              p.mb-0 Sistema de informacin de mercados
            li.d-flex.mb-0
              i.fas.fa-plus.mas
              p.mb-0 Formularios
            li.d-flex.mb-0
              i.fas.fa-plus.mas
              p.mb-0 Correo electrnico
            li.d-flex.mb-0
              i.fas.fa-plus.mas
              p.mb-0 Videos
            li.d-flex.mb-0
              i.fas.fa-plus.mas
              p.mb-0 Catlogos y portafolios
      .row.p-4(titulo="Seducir")
        .col-md-1
          figure
            img.img-a.img-t.d-none.d-lg-block(src='@/assets/curso/tema2/img58.svg', alt='Texto que describa la imagen')
        .col-md-10.col-lg-11.col-12.mb-4.mb-md-0
          p.mb-4 Es el momento de realizar un mayor nmero de conversaciones e interacciones, pero en este punto con clientes reales identificados plenamente. La conversacin incluye tambin a la fuerza de ventas y todo aquel que est involucrado con la prestacin de servicio. La creacin de contenidos interesantes y relevantes debe continuar, esta vez con informacin ms puntual y til para el mercado especfico. El proceso de venta en esta fase del proceso #[em inbound] ya es un hecho, para ello debe haber un protocolo de cotizacin y atencin al cliente muy bien estructurado. Por ltimo, es necesario pedir retroalimentacin permanente al cliente con el fin de conocer el nivel de satisfaccin que este tiene frente al producto, servicio y la atencin recibida. En la fase de seduccin se pueden utilizar las siguientes herramientas:
          ul.lista-ul--color
            li.d-flex.mb-0
              i.fas.fa-plus.mas
